




































































.card {
  .white-wrapper {
    @apply rounded-sm p-0;

    .thumb {
      @apply relative;
      height: 192px;

      svg {
        @apply absolute;
        width: 20px;
        right: 13px;
        top: 13px;
      }

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    .content {
      padding: 16px 21px 37px;
    }
  }
}
