







































.carousel {
  .carousel-inner {
    padding: 12.5px;
    backface-visibility: unset;
  }

  :last-child {
    button {
      @apply text-transparent rounded-full border-solid border-2 border-green;
      font-family: 'icomoon';
      height: 42px;
      width: 42px;
      padding: 0;

      &::before {
        @apply text-green;
      }

      &:first-child {
        left: -15px;

        &::before {
          content: '\e908';
        }
      }

      &:last-child {
        right: -15px;

        &::before {
          content: '\e909';
        }
      }
    }
  }
}
